<template>
  <div v-if="show" class="share-popup-overlay" @click="closePopup">
    <div class="share-popup" @click.stop>
      <div class="share-popup-header">
        <h3>Share Game Result</h3>
        <button class="close-button" @click="closePopup">&times;</button>
      </div>

      <div class="share-content">
        <div class="game-result">
          <div class="result-text">
            <span class="winner">üèÜ {{ winner }}</span>
            <span class="vs">vs</span>
            <span class="opponent">üèÜ {{ opponent }}</span>
          </div>
          <div class="game-link">
            Play BITCAT Hide & Seek: {{ gameUrl }}
          </div>
        </div>

        <div class="share-buttons">
          <button class="share-button twitter" @click="shareOnTwitter">
            Share on X
          </button>
          
          <button class="share-button copy" @click="copyToClipboard">
            üìã Copy Text
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'

const props = defineProps({
  show: {
    type: Boolean,
    default: false
  },
  winner: {
    type: String,
    required: true
  },
  opponent: {
    type: String,
    required: true
  },
  gameId: {
    type: String,
    required: true
  }
})

const emit = defineEmits(['close'])

const gameUrl = computed(() => {
  const baseUrl = window.location.origin
  return `${baseUrl}/#/hide-seek-games/${props.gameId}`
})

const shareText = computed(() => {
  return `üéÆ Just played BITCAT Hide & Seek!\n\nüèÜ: ${props.winner}\nvs\n${props.opponent}\n\nJoin the fun: ${gameUrl.value}\n\n@bitcat_sol_ #BITCAT #BITCAT_game #GameFi #Solana`
})

const shareOnTwitter = () => {
  const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(shareText.value)}`
  window.open(twitterUrl, '_blank')
}

const copyToClipboard = async () => {
  try {
    await navigator.clipboard.writeText(shareText.value)
    // alert('Text copied to clipboard!')
  } catch (err) {
    console.error('Failed to copy text: ', err)
  }
}

const closePopup = () => {
  emit('close')
}
</script>

