<template>
  <div class="game-card">
    <h3>Recent 10 Games</h3>
    <div class="history-list" v-if="games.length > 0">
      <div v-for="game in games" :key="game.gameId" class="history-item">
        <router-link 
          :to="`/hide-seek/${game.gameId}`" 
          class="history-link"
        >
          <div class="game-info">
            <div class="players">
              <span 
                :class="{ 
                  'player-tag': true,
                  'winner': game.winner === game.player1.id,
                  'loser': game.winner && game.winner !== game.player1.id,
                  'draw': !game.winner
                }"
              >
                {{ game.player1.userTag }}
              </span>
              <span class="vs">vs</span>
              <span 
                :class="{
                  'player-tag': true,
                  'winner': game.winner === game.player2.id,
                  'loser': game.winner && game.winner !== game.player2.id,
                  'draw': !game.winner
                }"
              >
                {{ game.player2.userTag }}
              </span>
            </div>
            <div class="game-details">
              <span class="stake">{{ game.stake }} {{ game.stakeType }}</span>
              <span class="time">{{ formatDate(game.createdAt) }}</span>
            </div>
          </div>
        </router-link>
      </div>
    </div>
    <div v-else class="no-games">
      <span></span>
      <p>No games played yet</p>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, watch, computed } from 'vue'
import { useStore } from 'vuex'

const store = useStore()
const games = ref([])

const formatDate = (date) => {
  return new Date(date).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
}

const initializeSocket = () => {
  // –ü–æ–¥–ø–∏—Å—ã–≤–∞–µ–º—Å—è –Ω–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∏—Å—Ç–æ—Ä–∏–∏ –∏–≥—Ä
  store.getters.socket.on('hs-history-update', (updatedGames) => {
    games.value = updatedGames
  })

  // –ó–∞–ø—Ä–∞—à–∏–≤–∞–µ–º –Ω–∞—á–∞–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
  store.getters.socket.emit('hs-get-history')
}

const socketConnected = computed(() => store.getters.isSocketConnected)
watch(socketConnected, (newValue) => {
  if (newValue) {
    console.log('Socket connected, initializing socket!!!!!!!!')
    initializeSocket()
  }
})

onMounted(() => {
  // initializeSocket()
})

onUnmounted(() => {
  // –û—Ç–ø–∏—Å—ã–≤–∞–µ–º—Å—è –æ—Ç —Å–æ–±—ã—Ç–∏–π –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
  if (store.getters.socket) {
    store.getters.socket.off('hs-history-update')
  }
})
</script>

