  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 600;
}

.winner {
  background: rgba(46, 204, 113, 0.1);
  color: #27ae60;
}

.loser {
  background: rgba(231, 76, 60, 0.1);
  color: #c0392b;
}

.draw {
  background: rgba(243, 156, 18, 0.1);
  color: #d35400;
}

