<template>
  <div class="hide-seek-page">
    <div class="game-header">
      <h1>üê± BITCAT Hide & Seek</h1>

      <div class="game-header__buttons">
        <button @click="showRules = true" class="rules-button">
          üìã Game Rules
        </button>
        <button @click="toggleSound" class="sound-button">
          {{ soundEnabled ? 'üîä' : 'üîá' }} Sound {{ soundEnabled ? 'on' : 'off' }}
        </button>
      </div>
     
    </div>

    <!-- Player Stats Dashboard -->
    <div class="dashboard" v-if="stats">
      <div class="stats-card">
        <div class="stats-header">Your Stats</div>
        <div class="stats-content">
          <div class="stats-item">
            <div class="stats-icon">üèÜ</div>
            <div class="stats-details">
              <div class="stats-label">Wins</div>
              <div class="stats-value">{{ stats.wins }}</div>
            </div>
          </div>
          <div class="stats-item">
            <div class="stats-icon">üíî</div>
            <div class="stats-details">
              <div class="stats-label">Losses</div>
              <div class="stats-value">{{ stats.losses }}</div>
            </div>
          </div>
          <div class="stats-item highlight">
            <div class="stats-icon">‚≠ê</div>
            <div class="stats-details">
              <div class="stats-label">Points</div>
              <div class="stats-value">{{ stats.totalPoints }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Game Creation Section -->
    <div v-if="!currentGame" class="game-section">
      <div class="section-header">
        <h2>üéÆ Game Options</h2>
      </div>

      <div class="game-options">
        <!-- Create Game Card -->
        <div class="game-card">
          <h3>Create New Game</h3>
          <div class="stake-type">
            <label>Stake with:</label>
            <button 
              :class="{ active: stakeType === 'points' }"
              @click="stakeType = 'points'"
            >
              ‚≠ê Cat Weight
            </button>
            <button 
              :class="{ active: stakeType === 'coins' }"
              @click="stakeType = 'coins'"
              :disabled="true"
            >
              ü™ô BITCAT Coins<small>(soon)</small>
            </button>
          </div>
          <div class="stake-amount-create-game">
            <label>Stake Amount:</label>
            <input 
              type="number" 
              v-model="stake" 
              :min="100"
              :max="stakeType === 'points' ? totalScore : stats?.totalPoints"
              @input="validateStake"
              placeholder="Enter stake amount"
            >
            <div class="stake-info" v-if="stakeType === 'points'">
              Your Cat Weight: {{ totalScore || 0 }}‚Çø
            </div>
            <div class="stake-info" v-else>
              Your BITCAT Coins: {{ stats?.totalPoints || 0 }}
            </div>
          </div>
          <button
            @click="createGame"
            :disabled="!canCreateGame || gameCreating"
            class="create-button"
          >
            üéÆ Create Game
          </button>
          <div v-if="!canCreateGame && totalScore < 100" class="warning-message">
            <span v-if="!store.getters.isAuthenticated">‚ö†Ô∏è Only authorized players can create a game. <router-link to="/my" class="more-info">Login ‚Üí</router-link></span>
            <span v-else>‚ö†Ô∏è You need at least 100 points to create a game</span>
          </div>
        </div>

        <!-- Available Games Card -->
        <div class="game-card available-games">
          <h3>Available Games</h3>
          <div class="games-list">
            <div v-if="availableGames.length > 0" class="games-container">
              <div 
                v-for="game in availableGames" 
                :key="game.gameId" 
                class="game-item"
              >
                <div class="game-header">
                  <div class="game-player-info">
                    <span class="player-icon">üë§</span>
                    <router-link :to="`/player/${game.player1?.userTag}`" class="player-name">
                      {{ game.player1?.userTag || 'Unknown Player' }}
                    </router-link>
                  </div>
                  <div class="time-ago">
                    <span class="time-icon">‚è∞</span>
                    {{ getTimeAgo(game.createdAt) }}
                  </div>
                </div>
                <div class="game-details">
                  <div class="stake-info">
                    <span class="label">Stake with:</span>
                    <span class="value">
                      {{ game.stakeType === 'points' ? '‚≠ê Cat Weight' : 'ü™ô BITCAT Coins' }}
                    </span>
                  </div>
                  <div class="stake-amount">
                    <span class="label">Amount:</span>
                    <span class="value">{{ game.stake }}{{ game.stakeType === 'points' ? '‚Çø' : '' }}</span>
                  </div>
                </div>
                <button 
                  @click="joinGame(game.gameId, game.player1?.userTag)" 
                  class="join-button"
                >
                  üéÆ Join Game
                </button>
              </div>
            </div>
            <div v-else class="no-games">
              <span>üò¥ No available games</span>
              <p v-if="store.getters.isAuthenticated">Create a new game to start playing!</p>
              <p v-else>Login to create a game.</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Active Game Section -->
    <div v-else class="game-section active">
      <div class="game-status-bar">
        <div class="game-info">
          <div class="game-status">
            <span class="status-label">Game Status:</span>
            <span class="phase-badge" :class="currentGame?.status">
              {{ gamePhase }}
              <span v-if="showTimer" class="timer">‚è±Ô∏è {{ timeLeft }}s</span>
            </span>
          </div>
          <div class="stake-display">
            <span class="stake-label">Stake:</span>
            <span class="stake-value">{{ currentGame?.stake }} {{ currentGame?.stakeType }}</span>
          </div>
        </div>

        <div v-if="!currentGame?.player2" class="invite-section">
          <div class="invite-link">
            <input 
              ref="inviteLinkInput"
              type="text" 
              :value="inviteLink" 
              readonly
              class="invite-input"
            >
            <button @click="copyInviteLink" class="copy-button">
              {{ copied ? '‚úì Copied' : 'Copy Link' }}
            </button>
          </div>
          <div class="invite-hint">Share this link with your friend to invite them to the game</div>
        </div>

        <div class="players-container">
          <div class="player-card active" :class="{ 'active': currentGame?.status === 'placing' }">
            <div class="player-content">
              <img src="@/assets/bitcat-hero.png" class="player-avatar" alt="Player Avatar">
              <div class="player-info">
                <span class="player-name">{{ store.state.session.userTag }}<small>(you)</small></span>
                <span class="online-status online">Online</span>
              </div>
            </div>
          </div>

          <div class="vs-container">
            <div class="vs-line"></div>
            <div class="vs-badge">VS</div>
            <div class="vs-line"></div>
          </div>

          <div class="player-card">
            <div class="player-content">
              <img src="@/assets/bitcat-hero.png" class="player-avatar" alt="Player Avatar">
              <div class="player-info">
                <span v-if="!opponentOnline" class="player-name">{{ opponentDisplayName }}</span>
                <span v-else class="player-name"><router-link target="_blank" :to="`/player/${opponentDisplayName}`">{{ opponentDisplayName || 'Unknown Player' }}</router-link></span>
                <span class="online-status" :class="{ 'online': currentGame?.status !== 'waiting' && opponentOnline }">
                  {{ opponentOnline ? 'Online' : 'Offline' }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="game-phase-hint" v-if="isSelectable">
        <div v-if="currentGame?.status === 'placing'" class="phase-hint hide-phase">
          <span class="emoji">üê±</span> Hide your cat in one of the boxes!
        </div>
        <div v-else-if="currentGame?.status === 'guessing'" class="phase-hint seek-phase">
          <span class="emoji">üîç</span> Find your opponent's cat!
        </div>
      </div>

      <div class="game-grid">
        <div class="grid-container">
          <button
            v-for="i in 4"
            :key="i"
            class="grid-box"
            :class="{
              'can-select': isSelectable,
              'hide-phase': isSelectable && currentGame?.status === 'placing',
              'seek-phase': isSelectable && currentGame?.status === 'guessing',
              'selected': selectedBox === i,
              'revealed': showResults && (player1Position === i || player2Position === i),
              'correct-guess': showResults && player2Position === player1Position && player2Position === i,
              'wrong-guess': showResults && player2Position === i && player2Position !== player1Position,
              'cat-position': showResults && player1Position === i && player2Position !== i
            }"
            @click="handleBoxClick(i)"
            :disabled="!isSelectable || selectedBox !== null"
          >
            <span v-if="showResults && player1Position === i" class="box-emoji">
              <img :src="BitcatImage" alt="Bitcat" class="bitcat-image">
            </span>
            <span v-else-if="showResults && player2Position === i" class="box-emoji">üîç</span>
            <span v-else class="box-number">{{ i }}</span>
          </button>
        </div>
      </div>

      <div v-if="showResults" class="game-results">
        <div class="result-message" :class="gameResult.status">
          {{ gameResult.message }}
        </div>
        <div class="prize-info" :class="gameResult.status">
          {{ gameResult.points }} {{ currentGame?.stakeType || 'points' }}
        </div>
      </div>

      <HideSeekResultPopup
        v-if="showResultPopup"
        :show="showResultPopup"
        :game-result="gameResult"
        :player1Position="player1Position"
        :player2Position="player2Position"
        :player1Guess="currentGame?.player1Guess"
        :player2Guess="currentGame?.player2Guess"
        :winnerTag="currentGame?.winnerTag"
        :player-name="store.getters.userTag"
        :is-creator="currentGame?.isCreator"
        :opponent-name="opponentDisplayName"
        :stake-type="currentGame?.stakeType || 'points'"
        @close="showResultPopup = false"
        @share="showSharePopup = true"
      />

      <div class="game-controls">
        <button v-if="gameEnded" class="control-button result" @click="showResultPopup = true">
          Game Result
        </button>
        <button v-if="gameEnded" class="control-button share" @click="showSharePopup = true">
          Share on X
        </button>
      </div>

      <div class="game-controls">
        <button v-if="canRematch" @click="requestRematch" class="rematch-button">
          üîÑ Rematch
        </button>
        <button @click="leaveGame" class="leave-button">
          üö™ Leave Game
        </button>
      </div>
    </div>

    <!-- Game History Section -->
    <GameHistory />

    <!-- Rules Modal -->
    <RulesHideSeek v-if="showRules" v-model="showRules" />
    <ShareHideSeekPopup
      v-if="showSharePopup"
      :show="showSharePopup"
      :winner="store.getters.userTag"
      :opponent="opponentDisplayName"
      :gameId="currentGame?.gameId"
      @close="showSharePopup = false"
    />
    <div v-if="errorMessage" class="error-message-container">
      <div class="error-message">
        <div class="error-content">
          <span class="error-icon">‚ö†Ô∏è</span>
          {{ errorMessage }}
        </div>
        <button @click="errorMessage = ''" class="close-error">√ó</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, watch } from 'vue'
import store from '@/vuex'
import RulesHideSeek from '@/components/RulesHideSeek.vue'
import GameHistory from '@/components/GameHistory.vue'
import BitcatImage from '@/assets/bitcat-hero.png'
import ShareHideSeekPopup from '@/components/ShareHideSeekPopup.vue'
import HideSeekResultPopup from '@/components/HideSeekResultPopup.vue'
import { useGameSound } from '@/composables/useGameSound'

const initSocket = async () => {
  await store.dispatch('initSocket')
}
// const store = useStore()
// let socket = null
const { playGameStartSound } = useGameSound()
const stats = ref(null)
const currentGame = ref(null)
const availableGames = ref([])
const stakeType = ref('points')
const stake = ref(100)
const selectedBox = ref(null)
const timeLeft = ref(0)
const showResults = ref(false)
const player1Position = ref(null)
const player2Position = ref(null)
// const gamePhase = ref('')
const opponent = ref(null)
const showRules = ref(false)
const errorMessage = ref('')
const opponentDisplayName = ref('Waiting for player...')
const opponentOnline = ref(false)
const inviteLinkInput = ref(null)
const copied = ref(false)
const gameCreating = ref(false)
const showSharePopup = ref(false)
const showResultPopup = ref(false)

const timeUpdateInterval = ref(null);
const currentTime = ref(Date.now());

const soundEnabled = computed(() => store.getters.soundEnabled)

const toggleSound = () => {
  store.commit('SET_SOUND_ENABLED', !soundEnabled.value)
}

const inviteLink = computed(() => {
  if (!currentGame.value?.gameId) return ''
  return `${window.location.origin}/game/#/hide-seek?game=${currentGame.value.gameId}`
})

const totalScore = computed(() => store.getters.totalScore || 0)

// Computed properties
const canCreateGame = computed(() => {
  // if (!stats.value) return false
  // if (!stake.value || stake.value < 10) return false
  
  if (stakeType.value === 'points') {
    // Check if user has enough totalScore for betting
    if (!totalScore.value || totalScore.value < 100) return false
    return stake.value <= totalScore.value
  }
  // For coins betting
  // if (!stats.value.totalPoints || stats.value.totalPoints < 100) return false
  return stake.value <= stats.value.totalPoints
})

const isSelectable = computed(() => {
  return currentGame.value?.status === 'placing' || currentGame.value?.status === 'guessing'
})

const showTimer = computed(() => {
  return currentGame.value?.status === 'placing' && timeLeft.value > 0
})

const opponentStatus = computed(() => {
  if (!currentGame.value) return ''
  if (currentGame.value.status === 'placing') return 'Placing cat...'
  if (currentGame.value.status === 'guessing') return 'Making guess...'
  return ''
})

const gameResult = computed(() => {
  if (!currentGame.value || !showResults.value) return { status: null, message: '' };
  
  // –ï—Å–ª–∏ –≤—Ä–µ–º—è –≤—ã—à–ª–æ –≤–æ –≤—Ä–µ–º—è —Ä–∞–∑–º–µ—â–µ–Ω–∏—è –∫–æ—Ç–æ–≤
  if (timeLeft.value <= 0 && currentGame.value.status === 'placing') {
    return { 
      status: 'cancelled',
      message: 'Time is up during placement! Game cancelled',
      minMessage: 'Game cancelled',
      points: '0'
    };
  }

  // –ï—Å–ª–∏ –µ—Å—Ç—å –ø–æ–±–µ–¥–∏—Ç–µ–ª—å
  if (currentGame.value.winner) {
    // –ï—Å–ª–∏ —è –ø–æ–±–µ–¥–∏—Ç–µ–ª—å
    console.log(currentGame.value)
    if (currentGame.value.winnerTag === store.getters.userTag) {
      return {
        status: 'win',
        message: 'You found your opponent! You won!',
        minMessage: 'You won!',
        points: `+${currentGame.value.stake}`
      };
    }
    // –ï—Å–ª–∏ —è –ø—Ä–æ–∏–≥—Ä–∞–ª
    return {
      status: 'lose',
      message: 'Your opponent found you! You lost!',
      minMessage: 'You lost!',
      points: `-${currentGame.value.stake}`
    };
  }

  // –ï—Å–ª–∏ –Ω–µ—Ç –ø–æ–±–µ–¥–∏—Ç–µ–ª—è - –Ω–∏—á—å—è
  return {
    status: 'draw',
    message: 'Game ended in a draw!',
    minMessage: 'Draw!',
    points: '+0'
  };
});



const gamePhase = computed(() => {
  if (!currentGame.value) return 'Waiting for game'
  
  console.log('Computing game phase, status:', currentGame.value.status);
  
  switch (currentGame.value.status) {
    case 'waiting':
      return 'Waiting for opponent'
    case 'placing':
      return 'Hide your cat'
    case 'guessing':
      return 'Find opponent\'s cat'
    case 'finished':
      return 'Game Results'
    case 'cancelled':
      return 'Game Cancelled'
    default:
      return currentGame.value.status
  }
})

const gameEnded = computed(() => {
  return currentGame.value?.status === 'finished' || currentGame.value?.status === 'cancelled'
})

// Methods
const createGame = async () => {
  console.log(store.getters.socket)
  try {
    store.getters.socket.emit('hs-create-game', {
      userTag: store.state.session.userTag,
      stake: stake.value,
      stakeType: stakeType.value
    })
    gameCreating.value = true
    setTimeout(() => {
      gameCreating.value = false
    }, 4000)
  } catch (error) {
    console.error('Error creating game:', error)
  }
}

const joinGame = async (gameId, creatorTag = null) => {
  if(creatorTag && creatorTag === store.getters.userTag) {
    errorMessage.value = 'You cannot join your own game';
    setTimeout(() => {
      errorMessage.value = '';
    }, 10000);
    leaveGame()

    return
  }
  try {
    console.log('Joining game:', gameId);
    store.getters.socket.emit('hs-join-existing-game', {
      gameId,
      userTag: store.state.session.userTag
    })
    
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∏–≥—Ä—ã
    currentGame.value = {
      id: gameId,
      status: 'placing' // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –Ω–∞—á–∞–ª—å–Ω—ã–π —Å—Ç–∞—Ç—É—Å
    }

    availableGames.value = availableGames.value.filter(game => game.gameId !== gameId);
  
    
  } catch (error) {
    console.error('Error joining game:', error)
    errorMessage.value = 'Failed to join game'
  }
}

const handleBoxClick = (boxNumber) => {
  if (!isSelectable.value) return
  
  selectedBox.value = boxNumber
  if (currentGame.value.status === 'placing') {
    console.log('placing cat')
    console.log(currentGame.value)
    store.getters.socket.emit('hs-place-cat', {
      gameId: currentGame.value.gameId,
      userTag: store.state.session.userTag,
      position: boxNumber
    })
  } else if (currentGame.value.status === 'guessing') {
    store.getters.socket.emit('hs-make-guess', {
      gameId: currentGame.value.gameId,
      userTag: store.state.session.userTag,
      guess: boxNumber
    })
  }
}

const requestRematch = () => {
  // Implement rematch logic
}

const leaveGame = () => {
  // console.log('Leaving game...')
  // console.log(currentGame.value)
  if (currentGame.value?.gameId && currentGame.value?.status !== 'finished') {
    store.getters.socket.emit('hs-leave-game', { userTag: store.getters.userTag, gameId: currentGame.value.gameId });
  }
  opponentDisplayName.value = 'Waiting for player...'
  opponentOnline.value = false
  currentGame.value = null
  showResults.value = false
  selectedBox.value = null

  console.log('Game cancelled:', currentGame.value)

   
  // if (currentGame.value.userTag !== store.getters.userTag) {
  //   console.log('Game cancelled!!!@@@@@:')
  //   errorMessage.value = 'Game cancelled by opponent';
  //   // –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Å–∫—Ä—ã–≤–∞–µ–º –æ—à–∏–±–∫—É —á–µ—Ä–µ–∑ 5 —Å–µ–∫—É–Ω–¥
  //   setTimeout(() => {
  //     errorMessage.value = '';
  //   }, 10000);
  //   console.log('Game cancelled:', currentGame.value)
  //   // currentGame.value = {}
  //   // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ —Ç–æ–ª—å–∫–æ –µ—Å–ª–∏ –∏–≥—Ä—É –æ—Ç–º–µ–Ω–∏–ª –¥—Ä—É–≥–æ–π –∏–≥—Ä–æ–∫
  //   // currentGame.value.status = 'cancelled'
  // }
}

const validateStake = () => {
  if (!stats.value) return
  
  const maxStake = stakeType.value === 'points' ? totalScore : stats.value.totalPoints
  if (stake.value > maxStake) {
    stake.value = maxStake
  }
}
const getTimeAgo = (date) => {
  const seconds = (Math.floor((currentTime.value - new Date(date)) / 1000) + 2);
  
  if (seconds < 60) {
    return `${seconds} second${seconds !== 1 ? 's' : ''} ago`;
  }
  
  const minutes = Math.floor(seconds / 60);
  if (minutes < 60) {
    return `${minutes} minute${minutes !== 1 ? 's' : ''} ago`;
  }
  
  const hours = Math.floor(minutes / 60);
  if (hours < 24) {
    return `${hours} hour${hours !== 1 ? 's' : ''} ago`;
  }
  
  const days = Math.floor(hours / 24);
  return `${days} day${days !== 1 ? 's' : ''} ago`;
}

const copyInviteLink = async () => {
  try {
    await navigator.clipboard.writeText(inviteLink.value)
    copied.value = true
    setTimeout(() => {
      copied.value = false
    }, 2000)
  } catch (err) {
    // Fallback –¥–ª—è —Å—Ç–∞—Ä—ã—Ö –±—Ä–∞—É–∑–µ—Ä–æ–≤
    inviteLinkInput.value.select()
    document.execCommand('copy')
    copied.value = true
    setTimeout(() => {
      copied.value = false
    }, 2000)
  }
}

// Socket event handlers
const initializeSocket = () => {
  store.getters.socket.on('hs-game-created', (data) => {
    console.log('Game created:', data)
    currentGame.value = data
  })

  store.getters.socket.on('hs-game-joined', (data) => {
    if(store.getters.userTag !== data.player2) {
      return
    }
    console.log('Game joined:', data)
    currentGame.value = data
    if (data.player1) {
      opponentDisplayName.value = data.player1
      opponentOnline.value = true
    }

    timeLeft.value = data.timeLeft
    startTimer()

    if (soundEnabled.value) {
      playGameStartSound()
    }
  })

  store.getters.socket.on('hs-game-started', (data) => {
    if(store.getters.userTag !== data.player1) {
      return
    }
    console.log('Game started!!!!!!!!:', data)

    currentGame.value = data
    if (data.player2) {
      opponentDisplayName.value = data.player2
      opponentOnline.value = true
    }
    //  {
    //   ...currentGame.value,
    //   ...data,
    // }
    console.log('Current game after start:', currentGame.value);
    // currentGame.value.status = 'placing'
    timeLeft.value = data.timeLeft
    startTimer()

    if (soundEnabled.value) {
      playGameStartSound()
    }
  })

  store.getters.socket.on('hs-player-left', (data) => {
    currentGame.value.status = data.status;
    showResults.value = true;
    errorMessage.value = data.message;
    // –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Å–∫—Ä—ã–≤–∞–µ–º –æ—à–∏–±–∫—É —á–µ—Ä–µ–∑ 5 —Å–µ–∫—É–Ω–¥
    setTimeout(() => {
      errorMessage.value = '';
    }, 5000);
    
    // –ï—Å–ª–∏ –≤—ã—à–µ–ª –æ–ø–ø–æ–Ω–µ–Ω—Ç, –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –µ–≥–æ –∫–∞–∫ –æ—Ñ—Ñ–ª–∞–π–Ω
    if (data.leftPlayer !== store.getters.userTag) {
      // console.log('Opponent left the game');
      opponentOnline.value = false;
    }
  });

  store.getters.socket.on('hs-guessing-phase', () => {
    currentGame.value.status = 'guessing'
    selectedBox.value = null
  })

  store.getters.socket.on('hs-game-result', (data) => {
    // console.log('Game result!!!!!!:', data)
    currentGame.value.status = data.status
    currentGame.value.stake = data.stake
    currentGame.value.stakeType = data.stakeType
    currentGame.value.winner = data.winner
    currentGame.value.winnerTag = data.winnerTag
    currentGame.value.player1Id = data.player1Id
    currentGame.value.player2Id = data.player2Id
    showResults.value = true
    player1Position.value = data.positions.player1
    player2Position.value = data.positions.player2
    currentGame.value.player1Guess = data.guesses.player1
    currentGame.value.player2Guess = data.guesses.player2
    // Update stats after result
    // loadStats()
  })

  store.getters.socket.on('hs-game-error', (data) => {
    errorMessage.value = data.message;
    // –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Å–∫—Ä—ã–≤–∞–µ–º –æ—à–∏–±–∫—É —á–µ—Ä–µ–∑ 5 —Å–µ–∫—É–Ω–¥
    setTimeout(() => {
      errorMessage.value = '';
    }, 5000);
  })

  // –ó–∞–ø—Ä–∞—à–∏–≤–∞–µ–º —Å–ø–∏—Å–æ–∫ –¥–æ—Å—Ç—É–ø–Ω—ã—Ö –∏–≥—Ä –ø—Ä–∏ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏–∏
  store.getters.socket.emit('hs-get-available-games');

  // –ü–æ–ª—É—á–∞–µ–º —Å–ø–∏—Å–æ–∫ –¥–æ—Å—Ç—É–ø–Ω—ã—Ö –∏–≥—Ä
  store.getters.socket.on('hs-available-games', (games) => {
    console.log('Received available games:', games);
    availableGames.value = games;
  });

  // –û–±–Ω–æ–≤–ª—è–µ–º —Å–ø–∏—Å–æ–∫ –ø—Ä–∏ –ø–æ—è–≤–ª–µ–Ω–∏–∏ –Ω–æ–≤–æ–π –∏–≥—Ä—ã
  store.getters.socket.on('hs-available-games-update', (newGame) => {
    if(availableGames.value.length === 0) {
      availableGames.value = [newGame]
    } else {
      availableGames.value.unshift(newGame);
    }
  });

  store.getters.socket.on('hs-scores-update', (data) => {
    console.log('Scores updated:', data);
    // Handle win/lose condition
    if (data.winner.userTag === store.getters.userTag) {
      store.commit('SET_TOTAL_SCORES', { totalScore: data.winner.totalScore });
    } else if (data.loser.userTag === store.getters.userTag) {
      store.commit('SET_TOTAL_SCORES', { totalScore: data.loser.totalScore });
    }
});

  // –£–¥–∞–ª—è–µ–º –∏–≥—Ä—É –∏–∑ —Å–ø–∏—Å–∫–∞, –∫–æ–≥–¥–∞ –∫ –Ω–µ–π –ø—Ä–∏—Å–æ–µ–¥–∏–Ω—è—é—Ç—Å—è
  store.getters.socket.on('hs-game-removed', (gameId) => {
    availableGames.value = availableGames.value.filter(game => game.gameId !== gameId);
  });

  store.getters.socket.on('hs-game-cancelled', (data) => {
    opponentDisplayName.value = 'Waiting for player...'
    opponentOnline.value = false
    currentGame.value = null
    showResults.value = false
    selectedBox.value = null

    console.log('Game cancelled:', data)

   
    if (data.userTag !== store.getters.userTag) {
      console.log('Game cancelled!!!@@@@@:')
      errorMessage.value = data.message;
      // –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ —Å–∫—Ä—ã–≤–∞–µ–º –æ—à–∏–±–∫—É —á–µ—Ä–µ–∑ 5 —Å–µ–∫—É–Ω–¥
      setTimeout(() => {
        errorMessage.value = '';
      }, 10000);
      console.log('Game cancelled:', data)
      // currentGame.value = {}
      // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ —Ç–æ–ª—å–∫–æ –µ—Å–ª–∏ –∏–≥—Ä—É –æ—Ç–º–µ–Ω–∏–ª –¥—Ä—É–≥–æ–π –∏–≥—Ä–æ–∫
      // currentGame.value.status = 'cancelled'
    }
  })
}

const startTimer = () => {
  const timer = setInterval(() => {
    if (timeLeft.value > 0) {
      timeLeft.value--
    } else {
      clearInterval(timer)
    }
  }, 1000)
}

// Load initial data
// const loadStats = async () => {
//   try {
//     const response = await fetch('/api/hide-seek/stats')
//     stats.value = await response.json()
//   } catch (error) {
