// Lifecycle hooks
onMounted(async() => {
  await initSocket()

  timeUpdateInterval.value = setInterval(() => {
    currentTime.value = Date.now();
  }, 1000);
})

onUnmounted(() => {
  if (timeUpdateInterval.value) {
    clearInterval(timeUpdateInterval.value);
  }
  store.dispatch('cleanupHideSeek')
})

const socketConnected = computed(() => store.getters.isSocketConnected)
watch(socketConnected, (newValue) => {
  if (newValue) {
    initializeSocket()
  }
})

// Add gameId prop
const props = defineProps({
  gameId: {
    type: String,
    default: null
  }
})

// Add watch for gameId prop to auto-join game
watch(() => props.gameId, (newGameId) => {
  if (newGameId && store.getters.socket && !currentGame.value?.gameId) {
    joinGame(newGameId)
  }
}, { immediate: true })

// –°–ª–µ–¥–∏–º –∑–∞ –∏–∑–º–µ–Ω–µ–Ω–∏—è–º–∏ –≤ –∏–≥—Ä–µ –∏ –æ–ø–ø–æ–Ω–µ–Ω—Ç–µ
